@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.App {
  text-align: center;
}

*{font-family: "Poppins";-webkit-tap-highlight-color: transparent;box-sizing: border-box;}

:root {
  --color-dark: #1B1B1B;
  --color-dark-100: #272727;
  --color-blue: #3471FF;
  --color-lite: #AFAFAF;
}

button {
  border: 0;
  outline: 0;
}

button:active {
  transform: scale(0.97);
}

.welcomeScreen {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: var(--color-dark);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: .3s ease;
}

.header-appname {
  font-size: 25px;
  color: #fff;
  font-weight: 300;
  margin-bottom: 18px;
}

.header-appname span{
  font-weight: 500;
}

.header-start {
  background-color: var(--color-blue);
  color: #fff;
  font-weight: 500;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 47px;
  border-radius: 10px;
  will-change: transform;
  transition: .25s ease;
  position: relative;
  overflow: hidden;
}

.header-start svg {
  margin-right: 10px;
}

.header-start:active {
  transform: scale(0.96);
}

.header-start_starting {
  pointer-events: none;
}

.loadingFilter {
  background-color: var(--color-blue);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.hide {
  opacity: 0;
  pointer-events: none;
}
.loadingCircle {
  border-radius: 50%;
  border-width: 3px;
  border-color: transparent;
  border-top-color: #fff;
  border-style: solid;
  height: 20px;
  width: 20px;
  animation: loading .7s linear infinite;
}
@keyframes loading {
  from{transform: rotateZ(0deg);}
  to{transform: rotateZ(360deg);}
}

.welcomeScreen-credits{
  position: absolute;
  bottom: 30px;
  font-size: 12px;
  font-weight: 400;
  color: var(--color-lite);
}
.welcomeScreen-credits span {
  font-weight: 600;
}

.welcomeScreen-started {
  opacity: 0;
  pointer-events: none;
  transform: translateX(-10px);
}

.mainUI {
  background-color: var(--color-dark);
  color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 30px;
}
.mainUI-title{
  font-size: 35px;
  font-weight: 500;
  width: 100%;
  text-align: left;
  margin-top: -10px;
  margin-bottom: 20px;
}

.mainUI-textarea {
  background-color: transparent;
  border: 0;
  outline: 0;
  resize: none;
  width: 100%;
  caret-color: gray;
  height: 60%;
  color: #fff;
}

.mainUI-textarea.question {
  height: fit-content;
}

.mainUI-secondaryButton{
  background-color: var(--color-dark);
  color: #fff;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 47px;
  width: 47%;
  border-radius: 10px;
  will-change: transform;
  transition: .25s ease;
  position: relative;
  overflow: hidden;
  border: 2px solid var(--color-blue);
}

.mainUI-primaryButton{
  background-color: var(--color-blue);
  color: #fff;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 47px;
  width: 100%;
  border-radius: 10px;
  will-change: transform;
  transition: .25s ease;
  position: relative;
  overflow: hidden;
}

.mainUI-bottomButtons {
  width: 100%;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dialog {
  position: absolute;
  z-index: 999;
  top: 30%;
  background-color: var(--color-dark-100);
  border-radius: 10px;
  left: 25px;
  right: 25px;
  padding: 25px;
  text-align: left;
  height: fit-content;
  transition: .3s ease;
}

.dialogName {
  font-size: 25px;
  font-weight: 500;
  margin-bottom: 10px;
  margin-top: -5px;
}

.closeDialog {
  background-color: var(--color-blue);
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 47px;
  width: 100%;
  border-radius: 10px;
  will-change: transform;
  transition: .25s ease;
  position: relative;
  overflow: hidden;
}

.dialogAnswer {
  margin-bottom: 30px;
  color: var(--color-lite);
  font-size: 14px;
}